import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { Observable } from 'rxjs';
import { first, retry } from 'rxjs/operators';
import { CategoryService } from 'src/app/_services/category.service';
import { ImageService } from 'src/app/_services/image.service';
import { QrCodeService } from 'src/app/_services/qrCode.service';
import { SubCategoryService } from 'src/app/_services/subCategory.service';
import { UtilService } from 'src/app/_services/util.service';
import { VenueService } from 'src/app/_services/venue.service';
 
@Component({
  selector: 'app-support-qr',
  templateUrl: './support-qr-category.component.html',
  styleUrls: ['./support-qr-category.component.css']
})
export class SupportQrCategoryComponent implements OnInit {
  category: any = [
  ];
  selectedCategory: any = {};
  subCategory: any = [];
  selectedSubCategory: any = {};
  pageIndex = 0;
  venue: any = {};
  qrData: any = {};
  registerForm: any;

  constructor(
    private categoryService: CategoryService,
    private subCategoryService: SubCategoryService,
    private utilService: UtilService,
    private imageService: ImageService,
    private venueService: VenueService,
    private activatedRoute: ActivatedRoute,
    private qrCodeService: QrCodeService,
    private toastrService: ToastrService

  ) {

  }
  filteredLessorOptions: Observable<any[]>;
  filteredCategoryOptions: Observable<any[]>;

  ngOnInit(): void {

    this.loadAllCategory({ type: "Support" }, "");

    this.activatedRoute.params.subscribe((params: Params) => {
      const id = params["id"];
      if (id) {
        this.loadQrData(id);
      }
    });



  }

  private loadQrData(id) {
    this.qrCodeService
      .getById(id)
      .pipe(first())
      .subscribe((data) => {
        if (data) {
          this.qrData = data;

          this.venueService
            .getById(this.qrData.venue)
            .subscribe((data: any) => {
              if (data) {
                this.venue = data;
                localStorage.setItem("venue", JSON.stringify(this.venue));
              }
            },
              (error) => {
                this.toastrService.error(error);
              });
        }
      },
        (error) => {
          this.toastrService.error(error);
        });
  }

  get f() {
    return this.registerForm.controls;
  }

  private loadAllCategory(query, param) {

    if (this.pageIndex == 0) {
      this.category = [];
    }
    this.categoryService
      .getAll(query, param)
      .pipe(retry(2))
      .subscribe((response) => {

        response.data.forEach((element) => {
          this.category.push(element);
        });
        if (response.data.length >= 10) {
          this.pageIndex++;
          this.loadAllCategory(
            query,
            "?sort=_id&orderBy=desc&skip=" + this.pageIndex + "&limit=10"
          );
        }
        this.filteredCategoryOptions = this.utilService.filterForm(this.f.category, this.category);
      });
  }

  selectSubCategory(item) {

    this.selectedSubCategory = item;
  }
  // private loadAllSubCategory(query, param) {
  //   this.subCategoryService
  //     .getAll(query, param)
  //     .pipe(retry(2))
  //     .subscribe((response) => {
  //       this.subCategory = response.data;
  //     });
  // }


  imgSrc(type, name) {
    return this.imageService.getDefaultImageUrl(type, name);
  }


}
