import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AuthGuard } from 'src/app/_helpers';
import { SupportMyTicketComponent } from 'src/app/public/scan-qr/support-qr/support-my-ticket/support-my-ticket.component';
import { SupportQrCategoryComponent } from 'src/app/public/scan-qr/support-qr/support-qr-category.component';
import { SupportQrSubCategoryComponent } from 'src/app/public/scan-qr/support-qr/support-qr-sub-category/support-qr-sub-category.component';
import { SupportTicketStatusComponent } from 'src/app/public/scan-qr/support-qr/support-ticket-status/support-ticket-status.component';
import { SupportTicketComponent } from 'src/app/public/scan-qr/support-qr/support-ticket/support-ticket.component';

const routes: Routes = [
  {
    path: "category/:id", component: SupportQrCategoryComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "sub-category/:id", component: SupportQrSubCategoryComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "new-ticket/:id", component: SupportTicketComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "my-ticket/:id", component: SupportMyTicketComponent,
    canActivate: [AuthGuard],
  },
  {
    path: "view-ticket/:id", component: SupportTicketStatusComponent,
    canActivate: [AuthGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class SupportRoutingModule { }
