import { Injectable } from "@angular/core";
import { HttpClient } from "@angular/common/http";
import { appConfig } from "../global.config";
import { Observable } from "rxjs";
const service = "marketing";
@Injectable({ providedIn: "root" })
export class SubCategoryService {
  constructor(private http: HttpClient) { }

  getAll(data, param): Observable<any> {
    return this.http.post<any[]>(
      `${appConfig.apiUrl}/${service}/sub-category` + param,
      data
    );
  }

  register(category: any) {
    return this.http.post(`${appConfig.apiUrl}/${service}/sub-category/register`, category);
  }
  update(category: any) {
    return this.http.put(`${appConfig.apiUrl}/${service}/sub-category`, category);
  }

  getById(id: String) {
    return this.http.get(`${appConfig.apiUrl}/${service}/sub-category/` + id);
  }

  delete(id: number) {
    return this.http.delete(`${appConfig.apiUrl}/${service}/sub-category/${id}`);
  }
  getWithBlogs(id: String) {
    return this.http.get(`${appConfig.apiUrl}/${service}/sub-category/blogs/category/` + id);
  }
}
