import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Params } from '@angular/router';
import { ToastrService } from 'ngx-toastr';
import { first } from 'rxjs/operators';
import { QrCodeService } from 'src/app/_services/qrCode.service';
  
import { VenueService } from 'src/app/_services/venue.service';

@Component({
  selector: 'app-support-ticket',
  templateUrl: './support-ticket.component.html',
  styleUrls: ['./support-ticket.component.css']
})
export class SupportTicketComponent implements OnInit {

  venue: any = {};
  qrData: any = {};
  constructor(private activatedRoute: ActivatedRoute,
    private venueService: VenueService,

    private qrCodeService: QrCodeService,
    private toastrService: ToastrService) { }

  ngOnInit(): void {

    this.activatedRoute.params.subscribe((params: Params) => {
      const id = params["id"];
      if (id) {
        this.loadQrData(id);
      }
    });
  }



  private loadQrData(id) {
    this.qrCodeService
      .getById(id)
      .pipe(first())
      .subscribe((data) => {
        if (data) {
          this.qrData = data;

          this.venueService
            .getById(this.qrData.venue)
            .subscribe((data: any) => {
              if (data) {
                this.venue = data;
                localStorage.setItem("venue", JSON.stringify(this.venue));
              }
            },
              (error) => {
                this.toastrService.error(error);
              });
        }
      },
        (error) => {
          this.toastrService.error(error);
        });
  }

}
